.header {
  padding: 16px 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 70;
  transition: transform 0.4s cubic-bezier(0.25, 0.1, 0.25, 1);

  @include sm-max {
    padding: 8px 0;
  }

  &--sticky {
    background-color: rgba(#fff, 0.75);
    border-bottom: 0.33px solid $light-stroke;
    backdrop-filter: blur(25px);

    @media (prefers-color-scheme: dark) {
      background-color: rgba(25, 25, 29, 0.8);
    }

    @include sm-max {
      transform: translateY(-64px);
    }

    .button {

      &:hover,
      &--active {
        background-color: $base;
      }
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include sm-max {
      flex-wrap: wrap;
    }
  }
}

.menu {
  @include lg-min {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  @include sm {
    display: none;
  }

  @include sm-max {
    order: 99;
    width: 100%;
    padding: 4px;
    border-radius: 50px;
    background-color: $light-bg;
    margin-top: 20px;
  }

  &__list {
    display: flex;
    align-items: center;

    @include sm-max {
      width: 100%;
    }
  }

  &__item {
    @include sm-max {
      flex-grow: 1;
    }

    &:not(:last-child) {
      margin-right: 12px;

      @include xs-max {
        margin-right: 0;
      }
    }
  }

  .button {
    @include sm-max {
      width: 100%;
      justify-content: center;
      color: $light-text;
    }

    @include xs-max {
      padding: 8px 12px;
      font-size: 15px;

      .icon {
        margin-right: 8px;
      }
    }

    &--active {
      @include sm-max {
        background-color: $base;
        box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.08);
        color: $dark-text;
      }
    }
  }
}