html {
  scroll-padding-top: 78px;
}
@media (max-width: 767px) {
  html {
    scroll-padding-top: 134px;
  }
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding-top: 78px;
  background-color: var(--base);
  font-family: "SFProText", "Helvetice", sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.33;
  letter-spacing: -0.4px;
  color: var(--light-text);
  font-feature-settings: "clig" off, "liga" off;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (max-width: 767px) {
  body {
    padding-top: 134px;
  }
}
body.lock {
  overflow: hidden;
}
body.overlay {
  overflow: hidden;
  position: relative;
}
body.overlay::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 90;
}

.main {
  flex-grow: 1;
  overflow-x: hidden;
  padding: 64px 0;
}
@media (max-width: 1023px) {
  .main {
    padding: 40px 0 56px;
  }
}
@media (max-width: 767px) {
  .main {
    padding: 20px 0 40px;
  }
}

.container {
  width: 100%;
  max-width: 1392px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.logo {
  display: block;
  transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
}
.logo:active {
  transform: scale(0.95);
}
.logo:hover {
  opacity: 0.8;
}
.logo img {
  display: block;
  height: 100%;
  object-fit: contain;
}

.custom-logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit;
  transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
}
.custom-logo:active {
  transform: scale(0.95);
}
.custom-logo:hover {
  opacity: 0.8;
}
.custom-logo img {
  display: block;
  height: 44px;
  object-fit: contain;
  margin-right: 16px;
}
.custom-logo__info {
  font-weight: 600;
}
.custom-logo__tagline {
  display: block;
  font-size: 11px;
  line-height: 1.18;
  text-transform: uppercase;
  letter-spacing: 0;
}
.custom-logo__title {
  display: block;
  font-size: 17px;
  line-height: 1.29;
  color: var(--dark-text);
}
@media (max-width: 767px) {
  .custom-logo--mob {
    display: block;
  }
}
@media (max-width: 767px) {
  .custom-logo--mob img {
    margin: 0 auto 16px;
  }
}
@media (max-width: 767px) {
  .custom-logo--mob .custom-logo__info {
    text-align: center;
  }
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  display: block;
  margin: 0;
}

.button {
  display: inline-flex;
  align-items: center;
  padding: 11px 24px;
  border: none;
  border-radius: 24px;
  font-size: 17px;
  font-weight: 600;
  line-height: 1.3;
  color: var(--dark-text);
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
}
.button:active {
  transform: scale(0.95);
}
.button .icon {
  margin-right: 12px;
}
.button--active, .button:hover {
  background-color: var(--light-bg);
}
.button--accent {
  background-image: linear-gradient(95deg, #AE58FF 0%, #802DFF 100%);
  box-shadow: 0px 4px 12px 0px rgba(128, 45, 255, 0.24);
  color: #fff;
}
.button--accent:hover {
  opacity: 0.8;
}
.button--accent .icon {
  filter: drop-shadow(0px 1px 4px rgba(255, 235, 69, 0.16));
}
.button--apple {
  background-color: #000;
  color: var(--base);
}
.button--apple:hover {
  background-color: #000;
  opacity: 0.8;
}
@media (prefers-color-scheme: dark) {
  .button--apple {
    background-color: #fff;
  }
  .button--apple:hover {
    background-color: #fff;
  }
}
.button--google {
  background-color: #FFF;
  position: relative;
}
.button--google::before {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: inherit;
  border: 2px solid var(--stroke);
  position: absolute;
  left: 0;
  top: 0;
}
@media (prefers-color-scheme: dark) {
  .button--google {
    background-color: #fff;
    color: var(--base);
  }
  .button--google::before {
    display: none;
  }
  .button--google:hover {
    background-color: #fff;
    opacity: 0.8;
  }
}
.button--red {
  background-color: var(--error);
  box-shadow: 0px 4px 12px 0px rgba(242, 0, 41, 0.24);
  color: #fff;
}
.button--red:hover {
  background-color: var(--error);
}

.arrows {
  display: flex;
  align-items: center;
}
.arrows__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: var(--light-bg);
  cursor: pointer;
  transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
}
.arrows__item:active {
  transform: scale(0.95);
