  margin: 8px auto;
}
.popup .loader::before {
  content: "";
  width: 32px;
  height: 32px;
  border-radius: inherit;
  border: 3px solid var(--accent);
  border-bottom-color: transparent;
  position: absolute;
  left: -3px;
  top: -3px;
  animation: rotation 1.5s linear infinite;
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.popup .loader .icon {
  display: none;
}
.popup .loader--ready {
  border: none;
}
.popup .loader--ready::before {
  display: none;
}
.popup .loader--ready::after {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: inherit;
  position: absolute;
  left: 0;
  top: 0;
  background-color: var(--accent);
  opacity: 0.12;
  z-index: -1;
}
.popup .loader--ready .icon {
  display: block;
}

.premium {
  max-width: 900px;
  top: 8vh;
  bottom: 8vh;
}
@media (max-width: 767px) {
  .premium {
    max-width: 500px;
  }
}
.premium__info {
  display: flex;
  justify-content: space-between;
  text-align: left;
  margin: 32px 0;
}
@media (max-width: 767px) {
  .premium__info {
    display: block;
  }
}
.premium__col {
  width: calc(50% - 20px);
}
@media (max-width: 767px) {
  .premium__col {
    width: 100%;
  }
  .premium__col:not(:last-child) {
    margin-bottom: 40px;
  }
}
.premium__benefits ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 17px;
  color: var(--dark-text);
}
.premium__benefits ul li {
  padding-left: 36px;
  position: relative;
}
.premium__benefits ul li::before {
  content: "";
  width: 24px;
  height: 24px;
  background-image: url(./../images/sprites/main.stack.svg#image-color-tick);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 24px;
  position: absolute;
  left: 0;
  top: -1px;
}
.premium__benefits ul li:not(:last-child) {
  margin-bottom: 16px;
}
.premium__benefits ul span {
  display: block;
  font-size: 15px;
  color: var(--light-text);
  margin-top: 4px;
}
.premium__subtitle {
  display: block;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.25;
  color: var(--dark-text);
  margin-bottom: 24px;
}
.premium__plans {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: -16px;
}
@media (max-width: 767px) {
  .premium__plans {
    padding: 0 32px;
    margin: 0 -32px;
    flex-wrap: nowrap;
    overflow-x: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  .premium__plans::-webkit-scrollbar {
    display: none;
    width: 0;
    height: 0;
  }
}
@media (max-width: 479px) {
  .premium__plans {
    padding: 0 20px;
    margin: 0 -20px;
  }
}
.premium__plan {
  flex-shrink: 0;
  width: calc(50% - 8px);
  cursor: pointer;
  margin-bottom: 16px;
  transition: opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
}
@media (max-width: 767px) {
  .premium__plan {
    min-width: 190px;
    width: auto;
  }
  .premium__plan:not(:last-child) {
    margin-right: 16px;
  }
}
.premium__plan:hover {
  opacity: 0.8;
}
.premium__plan--full {
  width: 100%;
}
@media (max-width: 767px) {
  .premium__plan--full {
    width: auto;
  }
}
.premium__plan input {
  display: none;
}
.premium__plan input:checked + .premium__item {
  border-color: var(--accent);
  position: relative;
}
.premium__plan input:checked + .premium__item::after {
  content: "";
  width: 24px;
  height: 24px;
  background-image: url(./../images/sprites/main.stack.svg#image-radio-icon);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 24px;
  position: absolute;
  right: 12px;
  top: 12px;
}
.premium__item {
  display: block;
  min-height: 110px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 12px 16px;
  border-radius: 16px;
  border: 2px solid var(--stroke);
}
.premium__tag {
  display: inline-block;
  padding: 2px 8px;
  background-image: linear-gradient(95deg, #AE58FF 0%, #802DFF 100%);
  border-radius: 25px;
  font-size: 13px;
  font-weight: 600;
  line-height: 1.38;
  letter-spacing: -0.08px;
  color: #fff;
  margin-bottom: 12px;
}
.premium__name {
  display: block;
  font-size: 17px;
  font-weight: 600;
  color: var(--dark-text);
  margin-top: auto;
}
.premium__price {
  display: inline-flex;
  align-items: center;
  margin-top: 2px;
}
.premium__price span {
  font-size: 13px;
  margin-right: 4px;
}
.premium--sale .premium__price {
  color: var(--accent);
}
.premium--sale .premium__price span {
  color: var(--light-text);
  text-decoration: line-through;
}

.user-profile__nav {
  left: 12px;
  background-color: transparent;
}
.user-profile__nav--active {
  background-color: var(--light-bg);
}
.user-profile__nav--active .icon {
  color: #000;
}
@media (prefers-color-scheme: dark) {
  .user-profile__nav--active .icon {
    color: #fff;
  }
}
.user-profile__nav--active .user-profile__menu {
  opacity: 1;
  visibility: visible;
}
.user-profile__menu {
  width: 155px;
  padding: 2px 16px;
  border-radius: 16px;
  background-color: #fff;
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);
  position: absolute;
  left: 0;
  bottom: -8px;
  transform: translateY(100%);
  opacity: 0;
  visibility: hidden;
}
@media (prefers-color-scheme: dark) {
  .user-profile__menu {
    background-color: #242428;
  }
}
.user-profile__menu li:not(:last-child) {
  border-bottom: 1px solid var(--light-stroke);
}
.user-profile__link {
  display: block;
  padding: 10px 0;
  font-size: 17px;
  color: var(--dark-text);
  text-decoration: none;
}
.user-profile__link:hover {
  opacity: 0.8;
}
.user-profile__link--red {
  color: var(--error);
}
.user-profile__link--accent {
  color: var(--accent);
}
.user-profile__inner {
  padding: 0 16px;
  border-radius: 16px;
  background-color: var(--light-bg);
  text-align: left;
}
.user-profile__inner li:not(:last-child) {
  border-bottom: 1px solid var(--light-stroke);
}
.user-profile__inner .user-profile__link {
  padding: 16px 0;
}
.user-profile__info {
  padding: 16px 0;
  font-size: 17px;
  color: var(--dark-text);
}
.user-profile__info span {
  display: block;
  font-size: 15px;
  color: var(--light-text);
  margin-top: 2px;
}
.user-profile__premium {
  text-align: left;
}
.user-profile__premium .premium__price {
  color: var(--accent);
}
.user-profile__premium .premium__price span {
  color: var(--light-text);
  text-decoration: line-through;
}
.user-profile__premium + .popup__buttons {
  margin-top: 16px;
}

.delete .popup__image {
  margin-bottom: 28px;
}
.delete .popup__image img {
  display: block;
  margin: 0 auto;
  object-fit: contain;
}
.delete .popup__buttons {
  display: flex;
}
.delete .popup__buttons > *:not(:last-child) {
  margin-right: 12px;
  margin-bottom: 0;
}

.input input {
  display: block;
  width: 100%;
  padding: 10px 24px;
  border-radius: 24px;
  background-color: var(--light-bg);
  border: none;
  font-size: 17px;
  text-align: center;
  color: var(--dark-text);
}
.input input::placeholder {
  color: var(--light-text);
}
.input__error {
  display: block;
  font-size: 13px;
  letter-spacing: -0.08px;
  text-align: center;
  margin-top: 6px;
}
.input--error {
  color: var(--error);
}
.input--error input {
  background-color: rgba(242, 0, 41, 0.08);
  color: var(--error);
}

.single__wrap {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
}
.single__wrap a {
  color: var(--accent);
  text-decoration: none;
  transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
}
.single__wrap a:hover {
  opacity: 0.8;
}
.single__wrap p,
.single__wrap ul,
.single__wrap ol {
  margin: 0;
}
.single__wrap > *:not(:last-child) {
  margin-bottom: 12px;
}
.single__wrap h1,
.single__wrap h2,
.single__wrap h3,
.single__wrap h4,
.single__wrap h5,
.single__wrap h6 {
  font-weight: 600;
  line-height: 1.2;
  color: var(--dark-text);
  margin: 40px 0 12px;
}
.single__wrap h1 {
  margin: 0 0 48px;
}
.single .back {
  display: inline-flex;
  align-items: center;
  margin-bottom: 12px;
}
.single .back .icon {
  transform: scale(-1, 1);
  margin-right: 8px;
}

:root {
  --base: #fff;
  --base50: rgba(255, 255, 255, 0.5);
  --light-text: #808080;
  --dark-text: #292929;
  --light-bg: rgba(0, 0, 0, 0.04);
  --stroke: rgba(0, 0, 0, 0.12);
  --light-stroke: rgba(0, 0, 0, 0.08);
  --accent: #802DFF;
  --error: #F20029;
}

@media (prefers-color-scheme: dark) {
  :root {
    --base: #101014;
    --light-text: #787880;
    --dark-text: #F3F3FF;
    --light-bg: rgba(255, 255, 255, 0.06);
    --stroke: rgba(255, 255, 255, 0.12);
    --light-stroke: rgba(255, 255, 255, 0.08);
    --accent: #934DFF;
    --error: #D90B2E;
  }
}