.card {
  &__media {
    width: 198px;
    height: 198px;
    border-radius: 16px;
    margin-bottom: 16px;
    position: relative;
    overflow: hidden;
    cursor: pointer;

    @include lg-max {
      width: 160px;
      height: 160px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      background-color: rgba(#000, 0.16);
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      transition: opacity 0.25s cubic-bezier(0.25, 0.1, 0.25, 1)
    }

    &:active {
      .card__control {
        transform: scale(0.95);
      }
    }

    &:hover {
      &::before {
        opacity: 1;
      }

      .card__control {
        opacity: 1;
        visibility: visible;
      }
    }

    &--premium {
      .card__star {
        display: block;
      }
    }
  }

  &__star {
    display: none;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: #9C9C9C;
    position: absolute;
    top: 8px;
    left: 8px;
    z-index: 1;
    pointer-events: none;

    transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      border-radius: inherit;
      background-color: rgba(#252525, 0.55);
      backdrop-filter: blur(25px);
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
    }

    &::after {
      content: '';
      width: 100%;
      height: 100%;
      border-radius: inherit;
      background-image: url(./../images/sprites/main.stack.svg#image-star);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 18px;
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  &__control {
    display: block;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    color: #fff; 
    background-image: $accent-gradient;
    box-shadow: $accent-shadow;

    position: absolute;
    right: 12px;
    bottom: 12px;
    z-index: 1;

    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), visibility 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      border-radius: inherit;
      background-image: url(./../images/sprites/main.stack.svg#image-play-white);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 24px;
      position: absolute;
      left: 0;
      top: 0;
    }

    &::after {
      display: none;
      content: '';
      width: 100%;
      height: 100%;
      border-radius: inherit;
      background-image: url(./../images/sprites/main.stack.svg#image-pause-white);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 24px;
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  &__title {
    font-size: 17px;
    font-weight: 600;
    line-height: 1.3;
    color: $dark-text;
    margin-bottom: 2px;
  }

  &__subtitle {
    display: inline-block;
  }

  &--active {
    .card__control {
      opacity: 1;
      visibility: visible;

      &::before {
        display: none;
      }

      &::after {
        display: block;
      }
    }
  }

  &--featured {
    max-width: 506px;
    width: 100%;
    height: 320px;
    border-radius: 24px;
    position: relative;
    overflow: hidden;
    cursor: pointer;

    @include lg-max {
      max-width: 395px;
      height: auto;
      border-radius: 16px;
    }

    .card {
      &__media {
        width: 100%;
        height: 100%;
        border-radius: inherit;
        margin-bottom: 0;

        &::after {
          content: '';
          width: 100%;
          height: 50%;
          background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.64) 100%);
          position: absolute;
          left: 0;
          bottom: 0;
        }
      }

      &__control {
        right: 24px;
        bottom: 24px;
        opacity: 1;
        visibility: visible;

        @include sm-max {
          right: 16px;
          bottom: 16px;
        }
      }

      &__main {
        pointer-events: none;
        position: absolute;
        left: 24px;
        right: 92px;
        bottom: 21px;
        z-index: 1;

        @include lg-max {
          left: 16px;
          bottom: 16px;
        }
      }

      &__title {
        font-size: 20px;
        line-height: 1.25;
        color: #fff;
        margin-bottom: 3px;

        @include lg-max {
          font-size: 17px;
          margin-bottom: 2px;
        }
      }

      &__subtitle {
        font-size: 17px;
        line-height: 1.3;
        color: $base50;

        @include lg-max {
          font-size: 15px;
        }
      }
    }
  }
}