:root {
  --base: #fff;
  --base50: rgba(255, 255, 255, 0.5);
  --light-text: #808080;
  --dark-text: #292929;
  --light-bg: rgba(0, 0, 0, 0.04);
  --stroke: rgba(0, 0, 0, 0.12);
  --light-stroke: rgba(0, 0, 0, 0.08);
  --accent: #802DFF;
  --error: #F20029;
}

@media (prefers-color-scheme: dark) {
  :root {
    --base: #101014;
    --light-text: #787880;
    --dark-text: #F3F3FF;
    --light-bg: rgba(255, 255, 255, 0.06);
    --stroke: rgba(255, 255, 255, 0.12);
    --light-stroke: rgba(255, 255, 255, 0.08);
    --accent: #934DFF;
    --error: #D90B2E;
  }
}