//transitions
@mixin transition($active, $hover) {
  transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

  @if $active !='' {
    &:active {
      transform: scale(0.95);
    }
  }

  @if $hover !='' {
    &:hover {
      opacity: 0.8;
    }
  }
}

//media
@mixin xxl-min {
  @media (min-width: #{$width_xxl + 1}) {
    @content;
  }
}
@mixin xxl-max {
  @media (max-width: $width_xxl) {
    @content;
  }
}

@mixin xl-min {
  @media (min-width: #{$width_xl + 1}) {
    @content;
  }
}
@mixin xl {
  @media (min-width: #{$width_xl + 1}) and (max-width: $width_xxl) {
    @content;
  }
}
@mixin xl-max {
  @media (max-width: $width_xl) {
    @content;
  }
}

@mixin lg-min {
  @media (min-width: #{$width_lg + 1}) {
    @content;
  }
}
@mixin lg {
  @media (min-width: #{$width_lg + 1}) and (max-width: $width_xl) {
    @content;
  }
}
@mixin lg-max {
  @media (max-width: $width_lg) {
    @content;
  }
}

@mixin sm-min {
  @media (min-width: #{$width_sm + 1}) {
    @content;
  }
}
@mixin sm {
  @media (min-width: #{$width_sm + 1}) and (max-width: $width_lg) {
    @content;
  }
}
@mixin sm-max {
  @media (max-width: $width_sm) {
    @content;
  }
}

@mixin xs-min {
  @media (min-width: #{$width_xs + 1}) {
    @content;
  }
}
@mixin xs-max {
  @media (max-width: $width_xs) {
    @content;
  }
}

@mixin xxs-min {
  @media (min-width: #{$width_xxs + 1}) {
    @content;
  }
}
@mixin xxs-max {
  @media (max-width: $width_xxs) {
    @content;
  }
}

// fonts
@mixin font-face($font-name, $font-file, $font-weight) {
  @font-face {
    font-family: '#{$font-name}';
    font-display: swap;
    font-weight: #{$font-weight};
    src: url('../fonts/#{$font-file}.woff2') format('woff2'), url('../fonts/#{$font-file}.woff') format('woff')
  }
}