.player {
  height: 100dvh;
  display: flex;
  flex-direction: column;
  padding-bottom: 81px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 70;

  &::before {
    content: '';
    width: 100%;
    height: 81px;
    background-color: rgba(#fff, 0.75);
    border-top: 0.33px solid $light-stroke;
    backdrop-filter: blur(25px);
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;

    @media (prefers-color-scheme: dark) {
      background-color: rgba(25, 25, 29, 0.8);
    }

    @include xs-max {
      border-top: none;
      backdrop-filter: none;
      background-color: transparent;
      background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, rgba(255, 255, 255, 0.01) 10.61%, rgba(255, 255, 255, 0.03) 19.24%, rgba(255, 255, 255, 0.07) 26.2%, rgba(255, 255, 255, 0.12) 31.79%, rgba(255, 255, 255, 0.18) 36.32%, rgba(255, 255, 255, 0.25) 40.09%, rgba(255, 255, 255, 0.33) 43.4%, rgba(255, 255, 255, 0.41) 46.56%, rgba(255, 255, 255, 0.50) 49.88%, rgba(255, 255, 255, 0.59) 53.65%, rgba(255, 255, 255, 0.67) 58.18%, rgba(255, 255, 255, 0.76) 63.77%, rgba(255, 255, 255, 0.85) 70.73%, rgba(255, 255, 255, 0.93) 79.36%, #FFF 89.97%);

      @media (prefers-color-scheme: dark) {
        background-image: linear-gradient(180deg, rgba(16, 16, 20, 0.00) 0%, rgba(16, 16, 20, 0.01) 10.63%, rgba(16, 16, 20, 0.03) 19.27%, rgba(16, 16, 20, 0.07) 26.25%, rgba(16, 16, 20, 0.12) 31.85%, rgba(16, 16, 20, 0.18) 36.39%, rgba(16, 16, 20, 0.25) 40.16%, rgba(16, 16, 20, 0.33) 43.48%, rgba(16, 16, 20, 0.41) 46.65%, rgba(16, 16, 20, 0.50) 49.97%, rgba(16, 16, 20, 0.59) 53.74%, rgba(16, 16, 20, 0.67) 58.28%, rgba(16, 16, 20, 0.76) 63.88%, rgba(16, 16, 20, 0.85) 70.86%, rgba(16, 16, 20, 0.93) 79.5%, #101014 90.13%);
      }
    }
  }

  &__bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 81px;
    padding: 16px;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 71;
    transition: background-color 0.2s;
    pointer-events: all;
    will-change: transform;

    @include xs-max {
      background-color: rgba(#fff, 0.75);
      backdrop-filter: blur(25px);
      box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);
      left: 8px;
      right: 8px;
      bottom: 8px;
      border-radius: 12px;
      padding: 12px;
      height: 64px;

      @media (prefers-color-scheme: dark) {
        background-color: rgba(25, 25, 29, 0.8);
      }
    }

    .player__playback {
      @include lg-max {
        display: none;
      }
    }

    .player__prev,
    .player__next {
      @include xs-max {
        display: none;
      }
    }

    .player__play {
      @include xs-max {
        margin: 0 !important;
      }
    }
  }

  &__controls {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    @include lg-max {
      position: static;
      transform: none;
    }
  }

  &__buttons {
    display: flex;
    justify-content: center;

    &>*:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__button {
    padding: 0;
    background-color: transparent;
    border: none;
    color: $light-text;
    transition: color 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
    cursor: pointer;

    svg {
      display: block;
    }

    &:hover {
      color: $dark-text;
    }
  }

  &__play {
    .icon--pause {
      display: none;
    }

    &--pause {
      .icon--pause {
        display: block;
      }

      .icon--play {
        display: none;
      }
    }
  }

  &__playback {
    font-size: 11px;
    font-weight: 600;
    margin-top: 10px;
    position: relative;
    transition: color 0.2s;

    @include lg-max {
      padding: 0 40px;
    }

    @include xs-max {
      padding: 0;
    }

    &:hover {
      color: $dark-text;
    }

    input[type="range"] {
      @include lg-min {
        &::-webkit-slider-thumb {
          opacity: 0;
        }

        // for Firefox
        &::-moz-range-thumb {
          opacity: 0;
        }

        // for IE
        &::-ms-thumb {
          opacity: 0;
        }
      }
    }

    &:hover {
      input[type="range"] {
        &::-webkit-slider-thumb {
          opacity: 1;
        }

        // for Firefox
        &::-moz-range-thumb {
          opacity: 1;
        }

        // for IE
        &::-ms-thumb {
          opacity: 1;
        }
      }
    }
  }

  &__playback-bar {
    width: 500px;

    @include xl-max {
      width: 350px;
    }

    @include lg-max {
      width: 100%;
    }
  }

  &__progress-time {
    display: inline-block;
    position: absolute;
    left: -12px;
    top: 50%;
    transform: translate(-100%, -50%);

    @include lg-max {
      left: 0;
      transform: translate(0, -50%);
    }

    @include xs-max {
      top: initial;
      bottom: -10px;
      transform: translate(0, 100%);
    }
  }

  &__duration {
    display: inline-block;
    position: absolute;
    right: -12px;
    top: 50%;
    transform: translate(100%, -50%);

    @include lg-max {
      right: 0;
      transform: translate(0, -50%);
    }

    @include xs-max {
      top: initial;
      bottom: -10px;
      transform: translate(0, 100%);
    }
  }

  &__track {
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

    &:hover {
      opacity: 0.8;
    }
  }

  &__preview {
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    border-radius: 8px;
    margin-right: 16px;
    overflow: hidden;
    will-change: transform;

    @include xs-max {
      width: 40px;
      height: 40px;
      margin-right: 12px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    display: block;
    font-size: 17px;
    font-weight: 600;
    color: $dark-text;
    white-space: nowrap;
    margin-bottom: 2px;
    will-change: transform;

    @include xs-max {
      font-size: 15px;
      margin-bottom: 0;
    }
  }

  &__artist {
    display: block;
    white-space: nowrap;
    will-change: transform;

    @include xs-max {
      font-size: 13px;
    }
  }

  &__additional {
    display: flex;
    align-items: center;
    margin-left: auto;

    @include lg-max {
      display: none;
    }
  }

  &__sound {
    display: flex;
    align-items: center;

    &:hover {
      .player__button{
        color: $dark-text;
      }
    }

    .icon {
      margin-right: 12px;
    }

    .volume-off {
      display: none;
    }

    .volume-loud {
      display: none;
    }

    &.loud {
      .volume-loud {
        display: block;
      }
    }

    &.mute {
      .volume-on {
        display: none;
      }

      .volume-off {
        display: block;
      }
    }
  }

  &__full {
    margin-left: 64px;

    @include xl-max {
      margin-left: 32px;
    }
  }

  &__sound-bar {
    max-width: 100px;
    width: 100%;
  }

  &__top {
    max-width: 532px;
    width: 100%;
    padding: 16px;
    text-align: center;
    margin: 0 auto;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
  }

  &__caption {
    display: block;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    color: $base50;
    letter-spacing: 0;
    margin-bottom: 4px;
  }

  &__playlist {
    display: block;
    font-size: 17px;
    font-weight: 600;
    color: #fff;
  }

  &__close {
    display: none;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: rgba(#fff, 0.24);
    position: absolute;
    right: 24px;
    top: 24px;

    @include lg-max {
      display: flex;
    }

    @include xs-max {
      right: 20px;
      top: 20px;
    }
  }

  &__box {
    max-width: 532px;
    width: 100%;
    padding: 16px;
    text-align: center;
    margin: auto;
    opacity: 0;
    visibility: hidden;

    .player__title {
      display: inline-block;
      font-size: 22px;
      color: #fff;
      margin-bottom: 8px;

      @include lg-max {
        @media (orientation: landscape) {
          font-size: 18px;
        }
      }

      @include xs-max {
        font-size: 20px;
        margin-bottom: 3px;

        @media (orientation: landscape) {
          font-size: 17px;
        }
      }
    }

    .player__artist {
      max-width: 100%;
      width: max-content;
      font-size: 17px;
      color: $base50;
      margin: 0 auto;

      @include lg-max {
        @media (orientation: landscape) {
          font-size: 15px;
        }
      }
    }

    .player__playback {
      margin-top: 32px;

      @include lg-max {
        @media (orientation: landscape) {
          margin-top: 24px;
        }
      }
    }

    .player__buttons {
      margin: 48px 0 44px;

      @include lg-max {
        @media (orientation: landscape) {
          margin: 20px 0 12px;
        }
      }
    }

    .player__play svg {
      width: 48px;
      height: 48px;

      @include lg-max {
        @media (orientation: landscape) {
          width: 36px;
          height: 36px;
        }
      }
    }

    .player__prev,
    .player__next {
      svg {
        width: 36px;
        height: 36px;

        @include lg-max {
          @media (orientation: landscape) {
            width: 30px;
            height: 30px;
          }
        }
      }
    }

    .player__sound-bar {
      max-width: none;
    }
  }

  &__cover {
    max-width: 500px;
    width: 100%;
    border-radius: 24px;
    aspect-ratio: 1;
    box-shadow: 0px 12px 32px 0px rgba(0, 0, 0, 0.08);
    overflow: hidden;
    margin: 0 auto 32px;
    will-change: transform;

    @include lg-max {
      @media (orientation: landscape) {
        margin-bottom: 20px;
      }
    }

    @include xs-max {
      border-radius: 16px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__mob-controls {
    display: none;

    @include lg-max {
      display: block;
      opacity: 0;
      transition: opacity 0.2s ease-out;
    }
  }

  &__overlay {
    background-color: #000;
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100vh - 81px);
    bottom: 0;
    z-index: -1;
    opacity: 0;
    pointer-events: none;
    will-change: transform;

    &::after {
      content: '';
      width: 100%;
      height: 100%;
      background-image: linear-gradient(rgba(#000, 0.3) 0%, rgba(#000, 0.8) 100%);
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
    }
  }

  &--transparent {
    &::before {
      background-color: transparent;
    }
  }

  &:not(.player--open) {
    pointer-events: none;
  }

  &--open {
    input[type="range"] {
      background-color: rgba(255, 255, 255, 0.24);

      &::-webkit-slider-runnable-track,
      &::-moz-range-track,
      &::-ms-track,
      &::-ms-fill-lower,
      &:focus::-ms-fill-lower,
      &::-ms-fill-upper,
      &:focus::-ms-fill-upper {
        background-color: rgba(255, 255, 255, 0.24);
      }
    }

    .player {
      &__top {
        opacity: 1;
        visibility: visible;
      }

      &__box {
        opacity: 1;
        visibility: visible;
      }

      &__track {
        opacity: 0;
        visibility: hidden;
      }

      &__button {
        color: #fff;

        &:hover {
          opacity: 0.8;
        }
      }

      &__playback {
        color: $base50;
      }

      &__mob-controls {
        opacity: 1;
      }

      &__bar {
        @include xs-max {
          opacity: 0;
          visibility: hidden;
        }

        .player__buttons {
          @include lg-max {
            display: none;
          }
        }
      }
    }
  }

  &.animating {
    .player__bar {
      background-color: transparent;
      border-top: none;
      backdrop-filter: none;

      .player__buttons {
        @include lg-max {
          display: none;
        }
      }
    }

    .player__track,
    .player__full {
      pointer-events: none;
    }
  }
}

input[type="range"] {
  display: block;
  width: 100%;
  border-radius: 2px;
  background-color: $stroke;
  -webkit-appearance: none;

  &:focus {
    outline: none;
  }

  //thumb
  &::-webkit-slider-thumb {
    appearance: none;
    -webkit-appearance: none;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #fff;
    border: none;
    box-shadow: 0px 0px 0px 0.3px rgba(0, 0, 0, 0.12), 0px 1px 2px 0 rgba(0, 0, 0, 0.16);
    cursor: pointer;
    margin-top: -4px;
  }

  // for Firefox
  &::-moz-range-thumb {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #fff;
    border: none;
    box-shadow: 0px 0px 0px 0.3px rgba(0, 0, 0, 0.12), 0px 1px 2px 0 rgba(0, 0, 0, 0.16);
    cursor: pointer;
  }

  // for IE
  &::-ms-thumb {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #fff;
    border: none;
    box-shadow: 0px 0px 0px 0.3px rgba(0, 0, 0, 0.12), 0px 1px 2px 0 rgba(0, 0, 0, 0.16);
    cursor: pointer;
  }

  //track webkit
  &::-webkit-slider-runnable-track {
    width: 100%;
    height: 4px;
    border-radius: 4px;
    background-color: $stroke;
    cursor: pointer;
  }

  //track firefox
  &::-moz-range-track {
    width: 100%;
    height: 4px;
    border-radius: 4px;
    background-color: $stroke;
    cursor: pointer;
  }

  //track IE
  &::-ms-track {
    width: 100%;
    height: 4px;
    border-radius: 4px;
    background-color: $stroke;
    border: none;
    border-color: transparent;
    color: transparent;
    cursor: pointer;
  }

  &::-ms-fill-lower {
    background-color: $stroke;
    border: none;
    border-radius: 4px;
  }

  &:focus::-ms-fill-lower {
    background-color: $stroke;
  }

  &::-ms-fill-upper {
    background-color: $stroke;
    border: none;
    border-radius: 4px;
  }

  &:focus::-ms-fill-upper {
    background-color: $stroke;
  }
}