.featured-cards {
  margin-bottom: 32px;
  overflow: hidden;

  @include lg-max {
    margin-bottom: 56px;
  }

  @include sm-max {
    margin-bottom: 40px;
  }

  &+* {
    margin-top: -170px;
    position: relative;
    z-index: 1;

    @include lg-max {
      margin-top: 0;
    }
  }

  .container {
    @include xl-min {
      max-width: none;
      padding: 0 64px;
    }
  }

  &__title {
    width: 0;
    height: 0;
    font-size: 0;
    line-height: 0;
    overflow: hidden;
  }

  &__slider {
    padding-bottom: 170px;

    @include xl-max {
      margin: 0 -40px;
    }

    @include lg-max {
      padding-left: 24px;
      padding-bottom: 0;
      margin: 0 -24px;
    }

    @include sm-max {
      padding-left: 20px;
      margin: 0 -20px;
    }
  }

  .swiper-slide {
    width: 39%;
    position: relative;

    @include lg-max {
      max-width: 395px;
    }

    @include sm-max {
      max-width: 338px;
    }

    @include xxs-max {
      max-width: 90%;
    }

    .card {
      max-width: none;

      @include lg-min {
        transition: box-shadow 0.2s;

        &::after {
          content: '';
          width: 100%;
          height: 100%;
          background-color: #fff;
          opacity: 0;
          position: absolute;
          left: 0;
          top: 0;
          transition: opacity 0.2s;
          pointer-events: none;

          @media (prefers-color-scheme: dark) {
            background-color: rgba(#000, 0.5);
          }
        }
      }
    }

    &.swiper-slide-active {
      .card {
        @include lg-min {
          // box-shadow: 0 15px 15px 0 rgba(0, 0, 0, .15);
          box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 7px 15px 0px rgba(0, 0, 0, 0.10), 0px 27px 27px 0px rgba(0, 0, 0, 0.09), 0px 60px 36px 0px rgba(0, 0, 0, 0.05), 0px 107px 43px 0px rgba(0, 0, 0, 0.01), 0px 167px 47px 0px rgba(0, 0, 0, 0.00);
        }
      }
    }

    &:not(.swiper-slide-active) {
      @include lg-min {
        .card {
          pointer-events: none;

          &__main {
            opacity: 0;
          }

          &__control {
            opacity: 0;
          }

          &::after {
            opacity: 0.4;
          }
        }

        &.swiper-slide-visible .card {
          // box-shadow: 0 8px 8px 0 rgba(0, 0, 0, .15);
          box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 5.95px 12.75px 0px rgba(0, 0, 0, 0.10), 0px 22.95px 22.95px 0px rgba(0, 0, 0, 0.09), 0px 51px 30.6px 0px rgba(0, 0, 0, 0.05), 0px 90.95px 36.55px 0px rgba(0, 0, 0, 0.01), 0px 141.95px 39.95px 0px rgba(0, 0, 0, 0.00);

          &::after {
            opacity: 0.2;
          }
        }
      }
    }
  }

  .arrows {
    justify-content: center;
    margin-top: 24px;

    @include lg-max {
      display: none;
    }
  }
}