@use 'sass:math';

%svg-common {
	background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-angle {
	@extend %svg-common;
	background-position: 98.36065573770492% 19.607843137254903%;
	background-size: math.div(140, 18)*100% auto;
	width: 18px;

}
@mixin svg-pos-image-angle {
	background-position: 98.36065573770492% 19.607843137254903%;
}
@mixin svg-image-angle-hover {
	.svg-image-angle {
		@include svg-pos-image-angle-hover
	}
}

.svg-image-angle:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(18, 18)*100%;
}

.svg-image-angle-dims {
	width: 18px;
	height: 18px;
}

