.footer {
  background-color: $light-bg;
  padding-bottom: 81px;

  &__main {
    display: flex;
    justify-content: space-between;
    padding: 16px 0;

    @include lg-max {
      display: block;
      padding: 20px 0;
    }

    @include sm-max {
      padding-bottom: 24px;
    }
  }

  &__logos {
    display: flex;
    margin-right: 32px;

    @include lg-max {
      justify-content: center;
      margin: 0 auto 20px;
    }

    @include sm-max {
      flex-wrap: wrap;
      margin-bottom: 24px;
    }

    .custom-logo {
      margin-right: 40px;

      @include sm-max {
        width: 100%;
        margin-right: 0;
        margin-bottom: 16px;
      }
    }
  }

  &__apps {
    display: flex;
    align-items: center;

    .logo {
      &:not(:last-child) {
        margin-right: 12px;
      }
    }
  }

  &__menu {
    max-width: 260px;
    text-align: right;

    @include lg-max {
      max-width: none;
      text-align: left;
    }

    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;
      margin-bottom: -12px;

      @include lg-max {
        justify-content: center;
      }

      @include sm-max {
        display: block;
        margin-bottom: 0;
      }
    }

    li {
      margin-bottom: 12px;

      @include sm-max {
        margin-bottom: 0;
      }

      &:nth-child(2n+1) {
        padding-right: 40px;

        @include lg-max {
          padding-right: 0;
        }
      }

      &:not(:last-child) {
        @include lg-max {
          margin-right: 24px;
        }

        @include sm-max {
          margin-right: 0;
          margin-bottom: 16px;
        }
      }
    }
  }

  &__link {
    font-size: 12px;
    color: inherit;
    text-decoration: none;
    @include transition('', 'hover');

    @include lg-max {
      font-size: 15px;
    }

    @include sm-max {
      display: block;
      text-align: center;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0;
    border-top: 1px solid $light-stroke;

    @include lg-max {
      padding: 14px 0;
    }

    @include sm-max {
      flex-direction: column;
      padding: 20px 0 12px;
    }
  }

  &__copy {
    display: inline-block;
    font-size: 12px;
    margin-right: 32px;

    @include sm-max {
      order: 99;
      margin-right: 0;
      margin-top: 16px;
    }
  }

  &__langs {
    display: flex;
    align-items: center;

    li {
      position: relative;

      &:not(:last-child) {
        margin-right: 24px;

        &::after {
          content: '';
          width: 1px;
          height: 100%;
          background-color: $light-stroke;
          position: absolute;
          top: 0;
          right: -12px;
        }
      }
    }
  }

  &__lang {
    font-weight: 600;
    text-decoration: none;
    color: inherit;
    @include transition('', 'hover');

    &--active {
      color: $dark-text;
    }
  }
}