.popup {
  max-width: 500px;
  width: 100%;
  max-height: 100vh;
  // border-radius: 24px;
  // background-color: $base;
  position: fixed;
  top: 15vh;
  bottom: 15vh;
  left: 50%;
  transform: translateX(-50%);
  // top: 50%;
  // transform: translate(-50%, -50%);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1), visibility 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

  @include xs-max {
    background-color: $base;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 0;
    top: 0;
    bottom: 0;
    left: 0;
    transform: none;

    @media (prefers-color-scheme: dark) {
      background-color: #16161A;
    }
  }

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &__control {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: $light-bg;
    @include transition('active', 'hover');
    cursor: pointer;
    position: absolute;
    top: 12px;

    .icon {
      color: $light-text;
    }
  }

  &__back {
    left: 12px;

    .icon {
      transform: scale(-1, 1);
    }
  }

  &__close {
    right: 12px;
  }

  &__main {
    max-height: 100%;
    padding: 72px 32px 24px;
    border-radius: 24px;
    background-color: $base;
    text-align: center;
    overflow-y: scroll;

    @media (prefers-color-scheme: dark) {
      background-color: #16161A;
    }

    @include xs-max {
      background-color: transparent;
      border-radius: 0;
      padding-left: 20px;
      padding-right: 20px;
    }
  } 

  &__logo {
    display: block;
    width: 64px;
    height: 64px;
    filter: drop-shadow(0px 4px 12px rgba(0, 0, 0, 0.24));
    transform: translate3d(0, 0, 0);
    margin: 0 auto 32px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__title {
    display: block;
    font-size: 34px;
    font-weight: 600;
    line-height: 1.2;
    color: $dark-text;
    margin-bottom: 12px;

    span {
      display: block;
      background: linear-gradient(135deg, #F2D900 10%, #FFB81A 90%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  &__info {
    font-size: 17px;
    margin-bottom: 32px;

    p {
      margin: 0;
    }
  }

  &__buttons {
    &>*:not(:last-child) {
      margin-bottom: 16px;
    }

    .button {
      display: flex;
      justify-content: center;
      width: 100%;
    }
  }

  &__caption {
    display: block;
    margin-top: 24px;

    a {
      color: $accent;
      text-decoration: none;
      @include transition('', 'hover');
    }
  }

  .loader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 3px solid $stroke;
    color: $accent;
    position: relative;
    z-index: 1;
    margin: 8px auto;

    &::before {
      content: '';
      width: 32px;
      height: 32px;
      border-radius: inherit;
      border: 3px solid $accent;
      border-bottom-color: transparent;
      position: absolute;
      left: -3px;
      top: -3px;
      animation: rotation 1.5s linear infinite;
    }

    @keyframes rotation {
      0% {
        transform: rotate(0deg);
      }

      100% {
        transform: rotate(360deg);
      }
    }

    .icon {
      display: none;
    }

    &--ready {
      border: none;

      &::before {
        display: none;
      }

      &::after {
        content: '';
        width: 100%;
        height: 100%;
        border-radius: inherit;
        position: absolute;
        left: 0;
        top: 0;
        background-color: $accent;
        opacity: 0.12;
        z-index: -1;
      }

      .icon {
        display: block;
      }
    }
  }
}

.premium {
  max-width: 900px;
  top: 8vh;
  bottom: 8vh;

  @include sm-max {
    max-width: 500px;
  }

  &__info {
    display: flex;
    justify-content: space-between;
    text-align: left;
    margin: 32px 0;

    @include sm-max {
      display: block;
    }
  }

  &__col {
    width: calc(50% - 20px);

    @include sm-max {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 40px;
      }
    }
  }

  &__benefits {
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      font-size: 17px;
      color: $dark-text;

      li {
        padding-left: 36px;
        position: relative;

        &::before {
          content: '';
          width: 24px;
          height: 24px;
          background-image: url(./../images/sprites/main.stack.svg#image-color-tick);
          background-repeat: no-repeat;
          background-position: center;
          background-size: 24px;
          position: absolute;
          left: 0;
          top: -1px;
        }

        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }

      span {
        display: block;
        font-size: 15px;
        color: $light-text;
        margin-top: 4px;
      }
    }
  }

  &__subtitle {
    display: block;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.25;
    color: $dark-text;
    margin-bottom: 24px;
  }

  &__plans {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: -16px;

    @include sm-max {
      padding: 0 32px;
      margin: 0 -32px;
      flex-wrap: nowrap;

      overflow-x: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
        width: 0;
        height: 0;
      }
    }

    @include xs-max {
      padding: 0 20px;
      margin: 0 -20px;
    }
  }

  &__plan {
    flex-shrink: 0;
    width: calc(50% - 8px);
    cursor: pointer;
    margin-bottom: 16px;
    transition: opacity 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

    @include sm-max {
      min-width: 190px;
      width: auto;

      &:not(:last-child) {
        margin-right: 16px;
      }
    }

    &:hover {
      opacity: 0.8;
    }

    &--full {
      width: 100%;

      @include sm-max {
        width: auto;
      }
    }

    input {
      display: none;

      &:checked+.premium__item {
        border-color: $accent;
        position: relative;

        &::after {
          content: '';
          width: 24px;
          height: 24px;
          background-image: url(./../images/sprites/main.stack.svg#image-radio-icon);
          background-repeat: no-repeat;
          background-position: center;
          background-size: 24px;
          position: absolute;
          right: 12px;
          top: 12px;
        }
      }
    }
  }

  &__item {
    display: block;
    min-height: 110px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 12px 16px;
    border-radius: 16px;
    border: 2px solid $stroke;
  }

  &__tag {
    display: inline-block;
    padding: 2px 8px;
    background-image: $accent-gradient;
    border-radius: 25px;
    font-size: 13px;
    font-weight: 600;
    line-height: 1.38;
    letter-spacing: -0.08px;
    color: #fff;
    margin-bottom: 12px;
  }

  &__name {
    display: block;
    font-size: 17px;
    font-weight: 600;
    color: $dark-text;
    margin-top: auto;
  }

  &__price {
    display: inline-flex;
    align-items: center;
    margin-top: 2px;

    span {
      font-size: 13px;
      margin-right: 4px;
    }
  }

  &--sale {
    .premium__price {
      color: $accent;

      span {
        color: $light-text;
        text-decoration: line-through;
      }
    }
  }
}

.user-profile {
  &__nav {
    left: 12px;
    background-color: transparent;

    &--active {
      background-color: $light-bg;

      .icon {
        color: #000;

        @media (prefers-color-scheme: dark) {
          color: #fff;
        }
      }

      .user-profile__menu {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__menu {
    width: 155px;
    padding: 2px 16px;
    border-radius: 16px;
    background-color: #fff;
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);
    position: absolute;
    left: 0;
    bottom: -8px;
    transform: translateY(100%);
    opacity: 0;
    visibility: hidden;

    @media (prefers-color-scheme: dark) {
      background-color: #242428;
    }

    li {
      &:not(:last-child) {
        border-bottom: 1px solid $light-stroke;
      }
    }
  }

  &__link {
    display: block;
    padding: 10px 0;
    font-size: 17px;
    color: $dark-text;
    text-decoration: none;

    &:hover {
      opacity: 0.8;
    }

    &--red {
      color: $error;
    }

    &--accent {
      color: $accent;
    }
  }

  &__inner {
    padding: 0 16px;
    border-radius: 16px;
    background-color: $light-bg;
    text-align: left;

    li {
      &:not(:last-child) {
        border-bottom: 1px solid $light-stroke;
      }
    }

    .user-profile__link {
      padding: 16px 0;
    }
  }

  &__info {
    padding: 16px 0;
    font-size: 17px;
    color: $dark-text;

    span {
      display: block;
      font-size: 15px;
      color: $light-text;
      margin-top: 2px;
    }
  }

  &__premium {
    text-align: left;

    .premium__price {
      color: $accent;

      span {
        color: $light-text;
        text-decoration: line-through;
      }
    }

    &+.popup__buttons {
      margin-top: 16px;
    }
  }
}

.delete {
  .popup__image {
    margin-bottom: 28px;

    img {
      display: block;
      margin: 0 auto;
      object-fit: contain;
    }
  }

  .popup__buttons {
    display: flex;

    &>*:not(:last-child) {
      margin-right: 12px;
      margin-bottom: 0;
    }
  }
}


.input {
  input {
    display: block;
    width: 100%;
    padding: 10px 24px;
    border-radius: 24px;
    background-color: $light-bg;
    border: none;
    font-size: 17px;
    text-align: center;
    color: $dark-text;

    &::placeholder {
      color: $light-text;
    }
  }

  &__error {
    display: block;
    font-size: 13px;
    letter-spacing: -0.08px;
    text-align: center;
    margin-top: 6px;
  }

  &--error {
    color: $error;

    input {
      background-color: rgba(242, 0, 41, 0.08);
      color: $error;
    }
  }
}