.user-nav {
  display: flex;
  align-items: center;
  position: relative;

  &>* {
    &:not(:last-child) {
      margin-right: 12px;

      @include lg-max {
        margin-right: 24px;
      }
    }
  }

  .button {
    &[data-signin] {
      @include lg-max {
        display: none;
      }
    }

    &[data-premium] {
      @include lg-min {
        margin-right: 0;
      }

      @include sm-max {
        display: none;
      }
    }
  }

  &__button {
    display: none;
    flex-shrink: 0;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background-color: $light-bg;
    color: $light-text;
    position: relative;
    cursor: pointer;

    @include lg-max {
      display: block;
      margin-right: 0 !important;
    }

    &::before,
    &::after,
    span {
      content: '';
      width: 14px;
      height: 2px;
      border-radius: 4px;
      background-color: currentColor;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &::before {
      top: calc(50% - 5px);
    }

    &::after {
      top: calc(50% + 5px);
    }
  }

  &__wrap {
    display: none;
    flex-shrink: 0;
    width: 280px;
    padding: 0 16px;
    border-radius: 16px;
    background-color: #fff;
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);
    position: absolute;
    right: 0;
    bottom: -16px;
    transform: translateY(100%);
    opacity: 0;
    visibility: hidden;

    @media (prefers-color-scheme: dark) {
      background-color: #242428;
    }

    @include lg-max {
      display: block;
    }

    @include sm-max {
      width: 250px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    padding: 16px 0;
    font-size: 17px;
    font-weight: 600;
    line-height: 1.3;
    color: $dark-text;
    text-decoration: none;

    &:not(:last-child) {
      border-bottom: 1px solid $light-stroke;
    }

    &[data-premium] {
      display: none;

      @include sm-max {
        display: flex;
      }
    }
  }

  &__menu {
    display: none;
    padding-right: 40px;

    @include sm {
      display: flex;
    }

    &--active {
      color: $accent;
      position: relative;

      &::after {
        content: '';
        width: 24px;
        height: 24px;
        background-image: url(./../images/sprites/main.stack.svg#image-color-tick);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 24px;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  &__icon {
    flex-shrink: 0;
    width: 24px;
    height: 24px;
    margin-right: 12px;

    .icon,
    img {
      display: block;
      width: 100%;
      height: 100%;
    }

    img {
      border-radius: 50%;
      object-fit: cover;
    }
  }

  &--active {
    .user-nav {
      &__button {
        color: $dark-text;

        &::before {
          width: 16px;
          top: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
        }

        &::after {
          width: 16px;
          top: 50%;
          transform: translate(-50%, -50%) rotate(-45deg);
        }

        span {
          display: none;
        }
      }

      &__wrap {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.user {
  width: 44px;
  height: 44px;
  padding: 6px;
  overflow: hidden;

  @include lg-max {
    display: none;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
  }
}