@import "./normalize";
@import "./vars";
@import "./mixins";
@import "./fonts";
@import "./sprite_generated";

// From libs:
@import "../libs/swiper/swiper.css";

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-padding-top: 78px;

  @include sm-max {
    scroll-padding-top: 134px;
  }
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding-top: 78px;
  background-color: $base;
  font-family: $font;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.33;
  letter-spacing: -0.4px;
  color: $light-text;
  font-feature-settings: 'clig'off, 'liga'off;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @include sm-max {
    padding-top: 134px;
  }

  &.lock {
    overflow: hidden;
  }

  &.overlay {
    overflow: hidden;
    position: relative;

    &::before {
      content: '';
      background-color: rgba(#000, 0.8);
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 90;
    }
  }
}

.main {
  flex-grow: 1;
  overflow-x: hidden;

  padding: 64px 0;

  @include lg-max {
    padding: 40px 0 56px;
  }

  @include sm-max {
    padding: 20px 0 40px;
  }
}

.container {
  width: 100%;
  max-width: 1392px;
  margin: 0 auto;
  padding: 0 40px;

  @include lg-max {
    padding: 0 24px;
  }

  @include sm-max {
    padding: 0 20px;
  }
}

.logo {
  display: block;
  @include transition('active', 'hover');

  img {
    display: block;
    height: 100%;
    object-fit: contain;
  }
}

.custom-logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit;
  @include transition('active', 'hover');

  img {
    display: block;
    height: 44px;
    object-fit: contain;
    margin-right: 16px;
  }

  &__info {
    font-weight: 600;
  }

  &__tagline {
    display: block;
    font-size: 11px;
    line-height: 1.18;
    text-transform: uppercase;
    letter-spacing: 0;
  }

  &__title {
    display: block;
    font-size: 17px;
    line-height: 1.29;
    color: $dark-text;
  }

  &--mob {
    @include sm-max {
      display: block;
    }

    img {
      @include sm-max {
        margin: 0 auto 16px;
      }
    }

    .custom-logo__info {
      @include sm-max {
        text-align: center;
      }
    }
  }
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  display: block;
  margin: 0;
}

.button {
  display: inline-flex;
  align-items: center;
  padding: 11px 24px;
  border: none;
  border-radius: 24px;
  font-size: 17px;
  font-weight: 600;
  line-height: 1.3;
  color: $dark-text;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  @include transition('active', '');

  .icon {
    margin-right: 12px;
  }

  &--active,
  &:hover {
    background-color: $light-bg;
  }

  &--accent {
    background-image: $accent-gradient;
    box-shadow: $accent-shadow;
    color: #fff;

    &:hover {
      opacity: 0.8;
    }

    .icon {
      filter: drop-shadow(0px 1px 4px rgba(255, 235, 69, 0.16));
    }
  }

  &--apple {
    background-color: #000;
    color: $base;

    &:hover {
      background-color: #000;
      opacity: 0.8;
    }

    @media (prefers-color-scheme: dark) {
      background-color: #fff;

      &:hover {
        background-color: #fff;
      }
    }
  }

  &--google {
    background-color: #FFF;
    position: relative;

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      border-radius: inherit;
      border: 2px solid $stroke;

      position: absolute;
      left: 0;
      top: 0;
    }

    @media (prefers-color-scheme: dark) {
      background-color: #fff;
      color: $base;

      &::before {
        display: none;
      }

      &:hover {
        background-color: #fff;
        opacity: 0.8;
      }
    }
  }

  &--red {
    background-color: $error;
    box-shadow: 0px 4px 12px 0px rgba(242, 0, 41, 0.24);
    color: #fff;

    &:hover {
      background-color: $error;
    }
  }
}

.arrows {
  display: flex;
  align-items: center;

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: $light-bg;
    cursor: pointer;
    @include transition('active', 'hover');

    &--prev {
      .icon {
        transform: scale(-1, 1);
      }
    }

    &:not(:last-child) {
      margin-right: 16px;
    }

    &.swiper-button-disabled {
      opacity: 0.5;
    }

    .icon {
      width: 18px;
      height: 18px;
    }
  }
}

.hidden {
  display: none;
}