.single {
  &__wrap {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;

    a {
      color: $accent;
      text-decoration: none;
      @include transition('', 'hover')
    }

    p,
    ul,
    ol {
      margin: 0;
    }

    &>*:not(:last-child) {
      margin-bottom: 12px;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-weight: 600;
      line-height: 1.2;
      color: $dark-text;
      margin: 40px 0 12px;
    }

    h1 {
      margin: 0 0 48px;
    }
  }

  .back {
    display: inline-flex;
    align-items: center;
    margin-bottom: 12px;

    .icon {
      transform: scale(-1, 1);
      margin-right: 8px;
    }
  }
}