.slider-section {
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    @include lg-max {
      margin-bottom: 12px;
    }

    @include sm-max {
      display: block;
    }

    .arrows {
      @include sm-max {
        display: none;
      }
    }
  }

  &__title {
    font-size: 22px;
    font-weight: 600;
    line-height: 1.28;
    color: $dark-text;
    margin-right: 16px;

    @include lg-max {
      font-size: 20px;
    }

    @include sm-max {
      margin-right: 0;
    }
  }

  &__main {
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
  }

  .swiper-slide {
    max-width: 198px;
    width: 100%;
    margin-right: 24px;

    @include lg-max {
      max-width: 160px;
      margin-right: 16px;
    }
  }

  &+.slider-section {
    margin-top: 56px;

    @include lg-max {
      margin-top: 48px;
    }

    @include sm-max {
      margin-top: 32px;
    }
  }
}